<template>
  <form
    :id="formId"
    class="template-register__klaviyo is-hidden"
    method="post"
    aria-hidden="true"
    action="https://manage.kmail-lists.com/ajax/subscriptions/subscribe"
    accept-charset="UTF-8"
  >
    <input
      type="hidden"
      name="$fields"
      value="$source,$email,Accepts Marketing,$consent_method,$consent_form_id"
    >
    <input
      type="hidden"
      name="$list_fields"
      value="Accepts Marketing"
    >
    <input
      type="hidden"
      name="g"
      :value="klaviyoListID"
    >
    <input
      :id="`${ formId }_klaviyo_email`"
      type="email"
      name="$email"
      :value="customerEmail"
    >
    <input
      type="hidden"
      name="$consent_form_id"
      :value="klaviyoFormID"
    >
    <input
      type="hidden"
      name="$consent_method"
      :value="consentMethod"
    >
    <input
      :id="`${ formId }_klaviyo_marketing`"
      type="hidden"
      name="Accepts Marketing"
      :value="checkedConsent ? 'True' : 'False'"
    >
    <input
      type="hidden"
      name="$source"
      :value="source"
    >
  </form>
</template>

<script>

/**
 * Vue: Klaviyo form (Klaviyo-form)
 * -----------------------------------------------------------------------------
 * Klaviyo form.
 *
 * @param {Boolean} checkedConsent - Checked consent
 * @param {String} customerEmail - Customer email.
 * @param {String} formId - Klaviyo form ID.
 * @param {String} source - Klaviyo form source.
 *
 */

import { sendEmailSubscribe } from '~/helpers/datalayer'

export default {
  name: 'KlaviyoForm',

  props: {
    checkedConsent: {
      type: Boolean,
      default: false,
    },
    customerEmail: {
      type: String,
      default: '',
    },
    formId: {
      type: String,
      default: 'klaviyoDefault',
    },
    source: {
      type: String,
      default: '',
    },
    consentMethod: {
      type: String,
      default: '',
    },
  },

  emits: ['submit-shopify-form'],

  data() {
    return {
      klaviyoFormID: cnvs.settings.klaviyoFormID,
      klaviyoListID: cnvs.settings.klaviyoListID,
    }
  },

  methods: {

    /**
     * Submit Klaviyo form.
     */
    submit() {
      const { customerEmail, $el, $emit } = this
      const formAddress = $el.action
      const formData = new FormData($el)

      fetch(formAddress, {
        method: 'POST',
        body: formData,
      })
        .then((response) => response.json())
        .then(() => {
          sendEmailSubscribe(customerEmail)
          $emit('submit-shopify-form')
        })
        .catch((error) => window.console.error(error))
    },
  },
}
</script>

