/**
 * Helper: dataLayer
 * ------------------------------------------------------------------------------
 * Handle Universal Data Layer (UDL) action events.
 *
 * @namespace dataLayer
 */

import { cyrb53 } from '~/helpers/manipulate'

/**
 * Checks if global datalayer variable exists.
 */
export function checkForDatalayer() {
  if (!window.dataLayer) {
    window.dataLayer = []
  }
}

/**
 * Send add to cart event.
 * @param {Object} productData - Updated line item.
 * @param {Object} additionalData - Additional data to be included in the datalayer.
 */
export function addToCartEvent(productData, additionalData) {
  if (!productData ) {
    return
  }

  const { pageNameL5 } = cnvs.strings.dataLayer
  const { type } = cnvs.page
  checkForDatalayer()

  window.dataLayer.push({
    event: 'e_addToCart',
    ecommerce: {
      currencyCode: window.cnvs.store.currency,
      add: {
        ...additionalData,
        detail: {
          actionField: { list: pageNameL5[type] },
        },
        products: [ formatProductData(productData, 1) ],
      },
    },
  })
}

/**
 * Send remove from cart event.
 * @param {Object} productData - Updated line item.
 * @param {Number} quantity - Total number of updated item.
 */
 export function removeFromCartEvent(productData, quantity) {
  if (!productData) {
    return
  }

  checkForDatalayer()

  window.dataLayer.push({
    event: 'e_removeFromCart',
    ecommerce: {
      currencyCode: window.cnvs.store.currency,
      remove: {
        products: [ formatProductData(productData, quantity) ],
      },
    },
  })
}

/**
 * Format product details into UDL accepted product.
 * @param {Object} lineItem - Updated item from cart.
 * @param {Number} quantity - Total updated item quantity.
 * @returns {Object}
 */
function formatProductData(lineItem, quantity) {
  const productType = lineItem.type || lineItem.product_type

  return {
    name: lineItem.name || lineItem.product_title,
    id: lineItem.sku,
    price: (lineItem.price.amount || lineItem.price)  / 100,
    brand: 'HyperX',
    category: `MS/accessories/${productType.toLowerCase()
      .replaceAll(' ', '-')}`,
    variant: lineItem.title || lineItem.variant_title,
    quantity: quantity,
  }
}

/**
 * Send the order status event.
 */
  export function sendOrderStatus() {
  if (window.Shopify?.Checkout?.OrderStatus && window.Shopify?.checkout?.order_id) {
    window.dataLayer.push({
      event: 'e_orderStatus',
      orderNumber: Shopify.checkout.order_id,
    })
  }
}

/**
 * Send a coupon event when it is applied during checkout.
 * @param {String} coupon - The coupon being used during checkout.
 */
export function sendCouponApplied(coupon = '') {
  if (!coupon) {
    return
  }

  window.dataLayer.push({
    event: 'e_couponApplied',
    couponCode: coupon,
  })
}

/**
* Send email event when user subscribes to newsletter.
* @param {String} email - The user's email.
*/
export function sendEmailSubscribe(email) {
  if (!email) {
    return
  }

  checkForDatalayer()

  window.dataLayer.push({
    event: 'e_emailSubscribe',
    userID: cyrb53(email),
  })
}

/**
 * Send conversion event when user completes checkout.
 * @param {Object} orderData - Shopify.checkout object.
 */
export function sendConversion(orderData) {
  window.dataLayer.push({
    products: orderData.line_items.map((item) => ({
      price: Number(item.price),
      quantity: item.quantity,
      brandname: item.vendor || 'HyperX',
      sku: item.sku,
      upc: item.properties?._barcode,
    })),
    orderid: orderData.order_id,
    tags: [],
  })
}
