<template>
  <div class="newsletter-callout__form">
    <form
      :id="shopifyFormId"
      ref="shopifyForm"
      method="post"
      :action="`/contact#${shopifyFormId}`"
      accept-charset="UTF-8"
      class="contact-form"
      @submit.prevent="triggerKlaviyoForm"
    >
      <input
        type="hidden"
        name="form_type"
        value="customer"
      >
      <input
        type="hidden"
        name="utf8"
        value="âœ“"
      >
      <p
        v-if="isSuccessful"
        v-text="$string('general.newsletter_form.confirmation')"
      />
      <p
        v-if="formErrors.length > 0"
        v-text="formErrors"
      />

      <template v-if="!isSuccessful">
        <div class="newsletter-callout__input-group input-group">
          <input
            name="contact[tags]"
            value="newsletter"
            type="hidden"
          >

          <div class="newsletter-callout__field input-group__control">
            <input
              id="newsletter-callout-email"
              v-model="customerEmail"
              class="newsletter-callout__input"
              autocapitalize="off"
              autocorrect="off"
              name="contact[email]"
              :placeholder="$string('general.newsletter_form.placeholder')"
              required
              type="email"
            >

            <label
              class="newsletter-callout__label caption"
              for="newsletter-callout-email"
              v-text="$string('general.newsletter_form.label')"
            />
          </div>

          <div class="input-group__addon">
            <button
              id="newsletter-callout-subscribe"
              class="newsletter-callout__submit button button--primary"
              :class="{'is-disabled': requiresConsent && !checkedConsent}"
            >
              <span v-text="$string('general.newsletter_form.submit')" />
              <icon-arrow-up-right />
            </button>
          </div>
        </div>
        <div
          v-if="consentContent"
          class="checkbox__container newsletter-callout__consent"
          :class="{'newsletter-callout__consent--required': requiresConsent}"
        >
          <input
            v-if="requiresConsent"
            id="newsletter-callout-consent"
            v-model="checkedConsent"
            class="checkbox__input"
            name="consent"
            type="checkbox"
            :aria-checked="checkedConsent"
            required
          >
          <label
            class="body-2 checkbox__label"
            for="newsletter-callout-consent"
            v-html="consentContent"
          />
        </div>
      </template>
    </form>
    <klaviyo-form
      ref="klaviyoForm"
      form-id="klaviyoNewsletter"
      consent-method="Newsletter"
      source="Footer newsletter"
      :customer-email="customerEmail"
      :checked-consent="checkedConsent"
      @submit-shopify-form="submitForm"
    />
  </div>
</template>

<script>

/**
 * Vue: Newsletter form (newsletter-form)
 * -----------------------------------------------------------------------------
 * Newsletter form.
 *
 * @param {String} newsletterErrors - Errors from Shopify form
 * @param {Boolean} requiresConsent - Check if consent checkbox is required
 * @param {String} shopifyFormId - Shopify form ID
 *
 */
import IconArrowUpRight from '~icons/direction/arrow/up-right.svg'
import KlaviyoForm from '~async/newsletter-callout/klaviyo-form'

export default {
  name: 'NewsletterForm',

  components: {
    IconArrowUpRight,
    KlaviyoForm,
  },

  props: {
    newsletterErrors: {
      type: String,
      default: '',
    },
    requiresConsent: {
      type: Boolean,
      default: true,
    },
    consentContent: {
      type: String,
      default: '',
    },
    shopifyFormId: {
      type: String,
      default: '',
    },
  },

  data() {
    return {
      checkedConsent: false,
      customerEmail: '',
      formErrors: [],
      isSuccessful: false,
    }
  },

  mounted() {
    if (window.location.href.includes('customer_posted=true')) {
      this.isSuccessful = true
    }
  },

  methods: {

    /**
     * Submit Klaviyo Form.
     */
    triggerKlaviyoForm() {
      this.$refs.klaviyoForm.submit()
    },

    /**
     * Submit Shopify Form.
     */
    submitForm() {
      this.$refs.shopifyForm.submit()
    },
  },
}
</script>

